*{
  box-sizing: border-box;
  }
  .container-flex{
      display: flex;
      flex-wrap: wrap;
      justify-content: space-between;
  } 
  .container-inner{
    display: flex;
    justify-content: space-between;
} 
  
  th , td{
      text-align: left;
      vertical-align: middle;
      height: 30px;
  }
  th{
      background-color:lightblue;
      text-transform: uppercase;
  }
  tr{
      border-bottom: 1px solid;
  }
  
  table{
      width: 100%;
      border-collapse: collapse;
      margin-top: 10px;
  }
  button{
      border: none;
      padding: 5px 10px;
      margin:0px 10px;
      text-transform: capitalize;
      background-color: blue;
      color: #fff;
      border-radius: 5px;
      cursor: pointer;
      font-weight: bold
  }
  input[type=search]{
  border-radius: 10px;
  margin: 0px 10px;
  padding: 2px 10px;
  }
  .form-group{
    width: 100%;
    margin: 10px 2px;
  }
  .title{
    text-align: center;
    font-size: large;
    font-weight: bold;
  }
  label::after{
    content: "*";
    color: red;
    margin: 3px 0px;
  }
 input,select{
    display: block;
    margin: 5px 5px;
    padding: 2px 5px;
    width: 100%;
  }
  input[type=submit]{
    background-color: blue;
    border: none;
    color: #fff;
    padding: 5px;
    font-weight: bold;
    letter-spacing: 1px;
    cursor: pointer;
  }
  input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

/* Firefox */
input[type=number] {
  -moz-appearance: textfield;
}

.addnewtoll{
  position: fixed;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5);
  opacity: 0;
  visibility: hidden;
  transform: scale(1.1);
  transition: visibility 0s linear 0.25s, opacity 0.25s 0s, transform 0.25s;
}
.newentry{
  position: fixed;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5);
  opacity: 0;
  visibility: hidden;
  transform: scale(1.1);
  transition: visibility 0s linear 0.25s, opacity 0.25s 0s, transform 0.25s;
}
.modal-content {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  background-color: white;
  padding: 1rem 1.5rem;
  width: fit-content;
  min-width: 25em;
  border-radius: 0.5rem;
}
.show-modal {
  opacity: 1;
  visibility: visible;
  transform: scale(1.0);
  transition: visibility 0s linear 0s, opacity 0.25s 0s, transform 0.25s;
}
.close-button {
  float: right;
  width: 1.5rem;
  line-height: 1.5rem;
  text-align: center;
  cursor: pointer;
  border-radius: 0.25rem;
  background-color: lightgray;
}
a{
  text-decoration: none;
  color: #fff;
  font-weight: bold;
}
.delete{
  border-radius: 50%;
  cursor: pointer;
}
.container{
  opacity: 0;
display:none;
}